@import url('https://fonts.googleapis.com/css2?family=Overpass:wght@400;700&family=Poppins:wght@200;300;400&family=Roboto:wght@100;400&display=swap');

body{
  margin: 0;
  padding: 0;
  background-color: hsl(0, 2%, 92%);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Poppins', sans-serif;
  box-sizing: border-box;
}
.App{
  display: flex;
  flex-direction: column;
  background-color: white;
  width: 1200px;
  height: 720px;
  border-radius: 30px;
  border-bottom-right-radius: 200px;
  padding: 40px;
}
.dateContainer{
  display: flex;
  width: 800px;
  height: 200px;
  flex-direction: row;
  margin-bottom: 50px;
}
.dateForm{
  display: flex;
  justify-content: center;
  align-items: left;
  flex-direction: column;
  height: 150px;
  width: 200px;
  margin-right: 75px;
}
.dateInput{
  width: 200px;
  height: 80px;
  border: 1px solid hsl(0, 0%, 62%);
  border-radius: 10px;
  font-size: 2.25rem;
  padding-left: 20px;
  font-weight: 700;
}
.dateLabel{
  font-size: 1.25rem;
  color: hsl(0, 1%, 44%);
  font-weight: 700;
  height: 30px;
  letter-spacing: 4px;
  padding-bottom: 10px;
}
.dateInput:focus{
  outline: none;
  border: 1px solid hsl(259, 100%, 65%);
  caret-color:  hsl(259, 100%, 65%);
  caret-width: 4px;
  caret-height: 90px;
}
.age{
  font-size: 6.5rem;
  font-style: italic;
  font-weight: bolder;
}
strong{
  color: hsl(259, 100%, 65%);
}
.btn{
  width: 110px;
  height: 110px;
  border-radius: 50%;
  font-size: 4rem;
  border: none;
  background-color: hsl(259, 100%, 65%);
  cursor: pointer;
}
.btn:hover{
  background-color: black;
}
.calculate{
  height: 100px;
  width: 1200px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
.errorText{
  display: flex;
  position: absolute;
  color: red;
  font-style: italic;
}
@media screen and (max-width: 600px){
  body{
    font-size: 10px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    overflow: hidden;
  }
  .App{
    position: relative;
    width: 90%;
    height: 100%;
    display: flex;
  }

  .calculate{
    height: auto;
    width: auto;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .dateContainer{
    width: 85%;
    height: 20%;
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .dateForm{
    height: 90px;
    width: 140px;
    margin-right: 12px;
  }
  hr{
    display: none;
  }
  .btn{
    width: 60px;
    height: 60px;
    font-size: 2rem;
  }
  .age{
    font-size: 4rem;
    font-style: italic;
    font-weight: bolder;
  }
  .errorText{
    display: flex;
    position: absolute;
    color: red;
    font-style: italic;
  }
  .dateInput{
    width: 100px;
    height: 40px;
    border-radius: 10px;
    font-size: 1.25rem;
    padding-left: 0;
  }
}
@media screen and (max-width: 1024px){
  body{
    scale: 70%;
  }
}
@media screen and (max-width: 800px){
  body{
    scale: 60%;
  }
}